@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin gridAutofitGap ($min, $gap){
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($min, 1fr));
    gap:$gap;
}
@mixin gridAutofit ($min){
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($min, 1fr));    
}

