@use "../base/mixins" as m; 
@use "../base/variables" as v; 


.formulario{
    width: 100%;
    margin-top: 5rem;
    
    .campo{
        display: flex;
        width: 100%;
    }


    label{
        font-weight: 500;
        
        flex: 0 0 10rem;
        text-align: center;
        padding: 1rem 0;
    }

    
    .input{
        padding: 1rem;
        
               
        background-color: white;
        border: 1px solid #000;
        border-radius: 2px;
        margin-bottom: 2rem;
        flex: 1;

        &:focus{
            outline: 3px solid v.$azul;
        }

        @include m.tablet{
            width: 90%;

        }
    }

    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;  
        
        
    }

    
}



