@use "../base/mixins" as m; 
@use "../base/variables" as v; 

.acciones{
    display: flex;
    justify-content: space-around;
    @include m.telefono{
        flex-direction: column;
    }
    a{
        color: v.$blanco;       
        
        &:hover {
            color: darken( v.$blanco, 40% );
            cursor: pointer;
        }

        @include m.telefono{
            margin-bottom: 2rem;
        }
    }

}

.alerta{
    padding: .5rem;
    text-align: center;
    margin: 1rem auto; 
    color: white;  
    text-transform: uppercase;
    font-weight: 700;  
    width: 60%;
    &.error {
       
        background-color: crimson;
    }

    &.exito{
        background-color: v.$azul;
    }
}
