@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}

.nombre-pagina{
    font-weight: v.$bold;
    text-align: center;
    margin: 5rem 0;
    &::after{
        content: "";
        display: block;
        width: 40%;
        height: 5px;
        margin: 1rem auto;
        position: relative;
        background-color: white;
    }
}

.descripcion-pagina{
    text-align: center;

    &.mensaje{
        &::after{
            content: "";
            display: block;
            width: 40%;
            height: 3px;
            margin: 5rem auto;
            position: relative;
            background-color: white;

        } 
    }
}

