@use "../base/mixins" as m; 
@use "../base/variables" as v; 

.citas{
    list-style: none;
    padding: 0;
    li{
        padding: 1rem;
        border-top: 1px solid white;
        
        p{
            font-weight: v.$regular;
            
        }

        span, .servicio
        {
            font-weight: v.$bold ;
            color: v.$azul;
            
        }
       

    }
    
}

.total{

    span{
        font-weight: v.$bold ;
        color: v.$azul;
    }
  
}

.servicios{
    @extend .citas;
}
