@use "../base/mixins" as m; 
@use "../base/variables" as v;

.listado-servicios{
    
    
    
    @include m.gridAutofitGap(300px, 1.2rem);
    
    .servicio{
        border: 3px solid white;
        text-align: center;
        margin-top: 2rem;
        
        &:hover{
            box-shadow: 0px 0px 3px 4px rgba(86, 86, 86, 0.3);
            transform: translateY(-4px);
            transition: .5s;
        }

        .nombre-servicio{
            font-size: 2rem;
            font-weight: 700;
        }

        .precio-servicio{
            color: v.$azul;
            font-weight: 700;
        }
        
        &.seleccionado{
            background-color: v.$azul;
            p{
                color:v.$blanco;
                font-weight: 700;
            }
        }
    } 
}

.acciones-servicios{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    text-align: center;
    

    @include m.telefono{
        grid-template-columns: repeat(1,1fr);
        grid-template-rows: repeat(2,1fr);
    }
    
}



