@use "../base/variables" as v; 
@use "../base/mixins" as m; 

.contenedor-app{
    
    @include m.gridAutofit(350px); 
    @include m.tablet{
        grid-auto-rows: 30vh;
    }
}

.app{

    width: 95%;
    height: 100vh;
    padding: 2rem;
    margin: 0 auto;
    overflow-y: scroll;
    overflow-x: hidden;
    &::-webkit-scrollbar {
        display: none;
        }
   @include m.tablet{
    overflow:visible; 
   }
    
}

.barra{
    
    display: flex;
    align-items: center;
    justify-content: space-around;
    grid-template-columns: repeat(2,1fr);

    .boton-cerrar{
        @include m.boton2(v.$rojo, v.$blanco, 1rem , 1rem);
        border-radius: .5rem;
    }
    
    p{
        text-align: center;
        font-size: 2.5rem;
    }
}

.img-main{
    width: 100%;
    object-fit: cover;
    
    @include m.tablet{
        height: 25rem;
    }   
}

.barra-servicios{
    display: grid;
    grid-template-columns: repeat(3,1fr);

    @include m.tablet{
        display: inline;
    }
}



