@use 'variables' as v;
@use "mixins" as m; 

.boton-azul{
    @include m.boton(v.$azul, v.$blanco, 5rem auto, 1.5rem 4rem);
    border-radius: 5px; 
}

.boton-eliminar{
    @include m.boton3(v.$rojo, v.$blanco, 1rem auto, 1rem, 10% ); 
    border-radius: 5px; 
}

.boton-azul2{
    @include m.boton2(v.$azul, v.$blanco, 2rem auto, 1.5rem);
    border-radius: 5px; 
}

.boton-verde{
    @include m.boton3(v.$verde, v.$blanco, 1rem auto, 1rem, 10%); 
    border-radius: 5px; 
}



