/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: 100vh;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  font-weight: 700;
  text-align: center;
  margin: 5rem 0;
}
.nombre-pagina::after {
  content: "";
  display: block;
  width: 40%;
  height: 5px;
  margin: 1rem auto;
  position: relative;
  background-color: white;
}

.descripcion-pagina {
  text-align: center;
}
.descripcion-pagina.mensaje::after {
  content: "";
  display: block;
  width: 40%;
  height: 3px;
  margin: 5rem auto;
  position: relative;
  background-color: white;
}

.boton-azul {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  min-width: 35%;
  margin: 5rem auto;
  border-radius: 5px;
}
@media (max-width: 768px) {
  .boton-azul {
    width: auto;
  }
}
.boton-azul:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  min-width: 45%;
  margin: 1rem auto;
  border-radius: 5px;
}
@media (max-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.boton-azul2 {
  background-color: #0da6f3;
  padding: 1.5rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  min-width: 35%;
  margin: 2rem auto;
  border-radius: 5px;
}
@media (max-width: 768px) {
  .boton-azul2 {
    width: auto;
  }
}
.boton-azul2:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-verde {
  background-color: #329f00;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  min-width: 45%;
  margin: 1rem auto;
  border-radius: 5px;
}
@media (max-width: 768px) {
  .boton-verde {
    width: auto;
  }
}
.boton-verde:hover {
  background-color: #226c00;
  cursor: pointer;
}

.contenedor-app {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}
@media (max-width: 768px) {
  .contenedor-app {
    grid-auto-rows: 30vh;
  }
}

.app {
  width: 95%;
  height: 100vh;
  padding: 2rem;
  margin: 0 auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
.app::-webkit-scrollbar {
  display: none;
}
@media (max-width: 768px) {
  .app {
    overflow: visible;
  }
}

.barra {
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-template-columns: repeat(2, 1fr);
}
.barra .boton-cerrar {
  background-color: #cb0000;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  min-width: 35%;
  margin: 1rem;
  border-radius: 0.5rem;
}
@media (max-width: 768px) {
  .barra .boton-cerrar {
    width: auto;
  }
}
.barra .boton-cerrar:hover {
  background-color: #980000;
  cursor: pointer;
}
.barra p {
  text-align: center;
  font-size: 2.5rem;
}

.img-main {
  width: 100%;
  object-fit: cover;
}
@media (max-width: 768px) {
  .img-main {
    height: 25rem;
  }
}

.barra-servicios {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 768px) {
  .barra-servicios {
    display: inline;
  }
}

.seccion {
  padding: 5rem 0 1rem 0;
  display: none;
}
.seccion .texto-centrado {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.2rem;
}
.listado-servicios .servicio {
  border: 3px solid white;
  text-align: center;
  margin-top: 2rem;
}
.listado-servicios .servicio:hover {
  box-shadow: 0px 0px 3px 4px rgba(86, 86, 86, 0.3);
  transform: translateY(-4px);
  transition: 0.5s;
}
.listado-servicios .servicio .nombre-servicio {
  font-size: 2rem;
  font-weight: 700;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
  font-weight: 700;
}

.acciones-servicios {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
}
@media (max-width: 480px) {
  .acciones-servicios {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}

.contenido-resumen p {
  text-align: center;
}
.contenido-resumen::after {
  content: "";
  display: block;
  width: 60%;
  height: 3px;
  margin: 3rem auto 0 auto;
  position: relative;
  background-color: white;
}
.contenido-resumen h3 {
  padding-bottom: 1rem;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 800;
}

.contenedor-servicio {
  width: 60%;
  margin: 0 auto;
  border-bottom: 2px solid #0da6f3;
}
.contenedor-servicio span {
  color: #0da6f3;
  font-weight: 800;
}
.contenedor-servicio spanp {
  color: #0da6f3;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 2rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid white;
}
.citas li p, .servicios li p {
  font-weight: 400;
}
.citas li span, .servicios li span, .citas li .servicio, .servicios li .servicio {
  font-weight: 700;
  color: #0da6f3;
}

.total span {
  font-weight: 700;
  color: #0da6f3;
}

.formulario {
  width: 100%;
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  width: 100%;
}
.formulario label {
  font-weight: 500;
  flex: 0 0 10rem;
  text-align: center;
  padding: 1rem 0;
}
.formulario .input {
  padding: 1rem;
  background-color: white;
  border: 1px solid #000;
  border-radius: 2px;
  margin-bottom: 2rem;
  flex: 1;
}
.formulario .input:focus {
  outline: 3px solid #0da6f3;
}
@media (max-width: 768px) {
  .formulario .input {
    width: 90%;
  }
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.acciones {
  display: flex;
  justify-content: space-around;
}
@media (max-width: 480px) {
  .acciones {
    flex-direction: column;
  }
}
.acciones a {
  color: #FFFFFF;
}
.acciones a:hover {
  color: #999999;
  cursor: pointer;
}
@media (max-width: 480px) {
  .acciones a {
    margin-bottom: 2rem;
  }
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  margin: 1rem auto;
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  width: 60%;
}
.alerta.error {
  background-color: crimson;
}
.alerta.exito {
  background-color: #0da6f3;
}

.tabs {
  display: flex;
}
@media (max-width: 768px) {
  .tabs {
    display: block;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  width: 100%;
  margin-top: 2rem;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
  cursor: pointer;
}
.tabs button:hover {
  background-color: #0a85c3;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.paginacion {
  padding: 0 0 0 2rem;
  display: flex;
}
@media (max-width: 480px) {
  .paginacion {
    display: block;
  }
}
@media (max-width: 480px) {
  .paginacion button {
    margin: 1rem auto;
  }
}
.paginacion .ocultar {
  display: none;
}