@use "../base/mixins" as m; 
@use "../base/variables" as v; 

.tabs{
        display: flex;
    @include m.tablet{
        display: block;
    }

    button{
        background-color: v.$azul;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        width: 100% ;
        margin-top: 2rem;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold; 
        transition: background-color 300ms ease; 
        cursor: pointer;
        &:hover{
            background-color: darken($color: v.$azul, $amount: 10);
        }
        &.actual{
            background-color: v.$blanco;
            color: v.$azul;
        }
        
        
    }
}