@use "../base/mixins" as m; 
@use "../base/variables" as v;
   
.contenido-resumen{
    
    p{
        text-align: center;
    }
    &::after{
        content: "";
        display: block;
        width: 60%;
        height: 3px;
        margin: 3rem auto 0 auto;
        position: relative;
        background-color: white;
    }

    h3{
        padding-bottom: 1rem;
    }

    span{
        color: v.$azul;
        font-weight: 800; 
    }
}

.contenedor-servicio{
    width: 60%;
    margin: 0 auto;
    border-bottom: 2px solid v.$azul;

    span{
        color: v.$azul;
        font-weight: 800;
        &p{
        color: v.$azul;
        }
    }

    &:last-of-type{
        margin-bottom: 2rem;
    }

}
